/* stylelint-disable */
:root {
  --breakpoint-xs: 320px;
  --breakpoint-s: 576px;
  --breakpoint-m: 768px;
  --breakpoint-l: 992px;
  --breakpoint-xl: 1248px;

  --color-white: #ffffff;
  --color-black: #000000;
  --color-bus: #0000bf;
  --color-silver-light: #f7f7f8;
	--color-copper: #00d7a7;

  --default-border: 0px;
  --default-border-radius: 0px;

  --paragraph-margin-bottom: ;
}

body #cc-window.cc-window {
	background-color: var(--color-bus);
	color: var(--color-white);
	opacity: 1;
	transition: opacity 1s ease;
	padding: spacing(32px) spacing(24px) spacing(24px);
	position: fixed;
	bottom: 0;
	left: 0;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	z-index: 9999;
	max-width: 100%;
	width: 100%;

	@media only screen and (min-device-width: var(--breakpoint-l)) {
		flex-direction: row;
		flex-wrap: wrap;
	}

	.cc-message {
		display: block;
		margin: 0;
		margin-bottom: spacing(32px);
		width: 100%;

		// Added via filter
		.cookie-notice {
			margin-bottom: spacing(24px);

			:last-child {
				margin-bottom: 0;
			}

			&__title {
				color: var(--color-white);
				font-size: fontsize-heading(m);
				margin-bottom: spacing(24px);
			}

			@media only screen and (min-device-width: var(--breakpoint-l)) {
				font-size: fontsize-body(l);

				&__title {
					font-size: fontsize-heading(l);
				}
			}
		}
	}

	// Added via filter
	.cookie-categories-wrap {

		@media only screen and (min-device-width: var(--breakpoint-l)) {
			justify-content: right;
			flex-grow: 2;
			padding-right: spacing(32px);
			width: auto;
		}

		.cmplz-categories-wrap {
			margin-bottom: spacing(24px);

			.cc-category {
				color: var(--color-white) !important;
			}

			@media only screen and (min-device-width: var(--breakpoint-l)) {
				margin-bottom: 0;

				.cc-category {
					font-size: fontsize-body(l);
				}
			}
		}
	}

	.cc-compliance {
		flex-direction: column;
		width: 100%;

		.cc-btn {
			flex: 1;
			width: 100%;
		}

		@media only screen and (min-device-width: var(--breakpoint-s)) {
			flex-direction: row;

			.cc-btn {
				margin-bottom: 0;
				width: auto;

				+ .cc-btn {
					margin-left: spacing(layout-2-xs);
				}
			}
		}

		@media only screen and (min-device-width: var(--breakpoint-l)) {
			justify-content: right;
			width: auto;
		}
	}

	&.cc-invisible {
		opacity: 0;
	}

	&.cc-static {}

	&.cc-floating {}

	&.cc-theme-minimal {

		&:not(.cc-floating) {
			.cc-message {}
		}

		.cc-btn {
			&.cc-allow,
			&.cc-save,
			&.cc-dismiss {
			}
		}
	}

	&.cc-bottom {}

	.cc-btn {
		background-color: var(--color-bus) !important;
		border: 2px solid var(--color-white) !important;
		color: var(--color-white) !important;
		cursor: pointer;
		display: block;
		font-weight: 400;
		margin-bottom: spacing(s);
		padding: spacing(xs) spacing(32px);
		text-align: center;
		text-decoration: none;
		white-space: nowrap;

		&:focus,
		&:hover {
			background-color: var(--color-bus-dark) !important;
			border-color: var(--color-bus-dark) !important;
			color: var(--color-white) !important;
		}

		&.cc-allow {}

		&.cc-accept-all {
			background-color: var(--color-white) !important;
			border-color: var(--color-white) !important;
			color: var(--color-bus-dark) !important;

			&:focus,
			&:hover {
				background-color: var(--color-bus-dark) !important;
				border-color: var(--color-bus-dark) !important;
				color: var(--color-white) !important;
			}
		}

		&.cc-save {}

		&.cc-dismiss {}
	} // .cc-btn

} // .cc-window

.cookie-notice {
	p {
		width: max-content;
	}
}

body .cc-revoke {
	// display: none !important;
	background-color: var(--color-bus) !important;
	border-color: var(--color-bus);
	color: var(--color-white) !important;
	cursor: pointer;
	position: fixed;
  overflow: hidden;
  box-sizing: border-box;
  display: flex;
  flex-wrap: nowrap;
  z-index: 9999;
	padding: spacing(2-xs) spacing(s);

	&:focus,
	&:hover {
		background-color: var(--color-bus-dark);
	}

	@media screen and (max-width: 768px) {
		display: none !important;
	}

	&.cc-top {
	  top: 0;
	  left: spacing(s);
	}

	&.cc-bottom {
		bottom: 0;
		left: spacing(s);
	}
	&.cc-left {
		left: spacing(s);
		right: unset;
	}
	&.cc-right {
		right: spacing(s);
		left: unset;
	}
} // .cc-revoke

.cmplz-cookiebanner {
	border-radius: 0 !important;
	padding-bottom: 2em;
}

.cmplz-link {
	&.cookie-statement, &.privacy-statement {
		display: none !important;
	}
}

.cmplz-btn {
	border-radius: 0 !important;
}

.cc-animate {

	&.cc-revoke {
		transition: transform 1s ease;

		&:hover {}

		&.cc-top {}

		&.cc-bottom {}

		&.active {}

	}

} // .cc-animate

.cc-grower {
  max-height: 0;
  overflow: hidden;
  transition: max-height 1s;
} // .cc-grower

.cc-link {
	cursor: pointer;
	text-decoration: underline;
	display: inline-block;

	&:focus,
	&:hover {
		color: var(--color-bus-dark);
	}

	&:active,
	&:visited {
	  color: initial;
	}
} // .cc-link

.cc-header {
	font-size: 18px;
	font-weight: 700;
} // .cc-header

.cc-close {
	cursor: pointer;
	display: block;
	position: absolute;
	top: spacing(2-xs);
	right: spacing(2-xs);
	opacity: 0.9;

	&:focus,
	&:hover {
	  opacity: 1;
	}
} // .cc-close

.cc-highlight {

	.cc-btn {}

} // .cc-highlight

.cc-top {
	top: 0;
} // .cc-top

.cc-left {
	left: 0;
} // .cc-left

.cc-right {
	right: 0;
} // .cc-right

.cc-bottom {
	bottom: 0;
} // .cc-bottom

.cc-center {
	&.cc-revoke {
		bottom: 0;
		left: spacing(s);
		border-top-left-radius: var(--default-border-radius);
		border-top-right-radius:var(--default-border-radius);

		&.cc-animate {

			&:hover {}
		}
	}
} // .cc-center

.cc-floating {
} // .cc-floating

// Added via filter
.cookie-categories-wrap {
	display: flex;
	align-items: center;
	width: 100%;
}

.cc-banner {

	&.cc-top {
	  left: 0;
	  right: 0;
	  top: 0;
	}

	&.cc-bottom {
	  left: 0;
	  right: 0;
	  bottom: 0;
	}

	.cc-message {
	  display: block;
	  flex: 1 1 auto;
	  max-width: 100%;
	  margin-right: spacing(s);
	}

} // .cc-banner

.cc-compliance {
  display: flex;
  align-items: center;
  align-content: space-between;
} // .cc-compliance

@media print {
  .cc-revoke,
  .cc-window {
    display: none;
  }
}

#cc-window input[type="checkbox"],
#cmplz-manage-consent-container input[type="checkbox"] {
  width: initial;
  -webkit-appearance: checkbox;
}
#cc-window input[type="checkbox"]:checked::before,
#cmplz-manage-consent-container input[type="checkbox"]:checked::before {
  content: '';
}

/*
* Hide revoke button
* prevent overriding by script by using !important
*/
.cc-hidden {
  display: none !important;
}

#cc-window.cc-window,
#cmplz-manage-consent-container.cmplz-manage-consent-container {

	.cmplz-square-checkbox {

		&:checked {
			+ .cc-check {
				svg {
					path {
						stroke-dashoffset: 60;
						transition: all 0.3s linear;
					}

					polyline {
						stroke-dashoffset: 42;
						transition: all 0.2s linear;
						transition-delay: 0.15s;
					}
				}
			}
		}

		&:focus {
			+ .cc-check {
				svg {
					outline: -webkit-focus-ring-color auto 1px;
				}
			}
		}
	}

	input.cmplz-svg-checkbox,
	.cmplz-slider-checkbox input {
		display: block !important;
		opacity: 0 !important;
		height: 0 !important;
	}

	.cmplz-slider-checkbox {
		position: relative;
		display: inline-block;
		width: 40px;
		height: 21px;



		.cmplz-slider {
			background-color: var(--color-white) !important;
			position: absolute;
			cursor: pointer;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			transition: 0.4s;
			outline: var(--color-black) solid 1px;

			&:before {
				background-color: var(--color-black) !important;
				-webkit-mask-image: url(data:image/svg+xml;base64,);
				-webkit-mask-size: 21px;
				-webkit-mask-position: center;
				position: absolute;
				content: "";
				height: 15px;
				width: 15px;
				left: 4px;
				bottom: 3px;
				transition: 0.4s;
			}

			&:after {
				border-radius: 0;
				display: block;
				content: "";
				position: absolute;
				top: -5px;
				left: -5px;
				right: -5px;
				bottom: -5px;
			}


			&.cmplz-round {
				border-radius: 0;

				&:before {
					border-radius: 0;
				}
			}
		}

		.cmplz-slider-na {
			padding: 2px 8px;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			transition: 0.4s;

			&:before {
				position: absolute;
				content: "N/A";
				color: var(--color-white);
			}

			&.cmplz-round {
				border-radius: 20px;
			}
		}

		input {
			&:checked {
				+ .cmplz-slider {
					background-color: var(--color-copper) !important;
					outline: none;

					&:before {
						background-color: var(--color-copper) !important;
						transform: translateX(17px);
						-webkit-mask-image: url(data:image/svg+xml;base64,);
					}
				}
			}

			&[type="checkbox"] {
				&:focus {
					+ .cmplz-slider:after {
						box-sizing: content-box;
						border: 3px solid var(--color-coat-of-arms);
					}
				}
			}
		}
	}
	// .cmplz-slider-checkbox

	label {
		color: inherit;
		font-weight: normal;
		margin-bottom: 0;
		line-height: inherit;
		box-sizing: initial;

		&:not(.cc-check) {
			white-space: nowrap;
			margin-right: 15px;
			margin-left: 0;
			padding-left: 0;
		}
	}

	div {
		&.cc-check {
			display: inline-block;
			padding-bottom: 10px;
		}
	}

	.cc-check {
		display: inherit;
		transform: translate3d(0, 0, 0);
		cursor: pointer;
		position: relative;
		margin: auto 7px auto auto;
		width: 18px;
		height: 18px;
		-webkit-tap-highlight-color: transparent;

		svg {
			display: inherit;
			transform: translate3d(0, 0, 0);
			position: relative;
			z-index: 1;
			fill: none;
			stroke-linecap: round;
			stroke-linejoin: round;
			stroke-width: 3.5;
			transition: all 0.2s ease;
			stroke: #000;

			path {
				stroke-dasharray: 60;
				stroke-dashoffset: 0;
			}

			polyline {
				stroke-dasharray: 22;
				stroke-dashoffset: 66;
			}
		}

		&:before {
			content: "";
			position: absolute;
			opacity: 0;
		}

		&:hover {
			&:before {
				opacity: 1;
			}
		}
	}

	&.cc-theme-edgeless {
		.cmplz-categories-wrap {
			.cmplz-classic-checkbox {
				margin-left: 15px;
			}
		}
	}

	.cmplz-categories-wrap {

		label {
			box-sizing: initial;
		}

		.cc-check {
			cursor: pointer;
			position: relative;
			margin: auto 7px auto auto;
			width: 18px;
			height: 18px;
			-webkit-tap-highlight-color: transparent;

			&:before {
				content: "";
				position: absolute;
				opacity: 0;
			}

			&:hover {
				&:before {
					opacity: 1;
				}
			}

			svg {
				position: relative;
				z-index: 1;
				fill: none;
				stroke-linecap: round;
				stroke-linejoin: round;
				stroke-width: 3.5;
				transition: all 0.2s ease;
				stroke: #000;

				path {
					stroke-dasharray: 60;
					stroke-dashoffset: 0;
				}

				polyline {
					stroke-dasharray: 22;
					stroke-dashoffset: 66;
				}
			}
		}
	}
	// .cmplz-categories-wrap

	.cmplz-slider-checkbox {
		vertical-align: top;
		margin-right: spacing(s);
	}

	.cmplz-consent-checkbox {
		margin-right: 15px;
	}
}

.cmplz-manage-consent-container {

	> div {
		margin-bottom: spacing(s);
	}

}

/* Soft Cookie wall */
.cmplz-soft-cookiewall {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(var(--color-black), 0.7);
  z-index: 9999;
} // .cmplz-soft-cookiewall

.cmplz-document {

	.cmplz-soft-cookiewall {
	  background-color: transparent;
	}

	.cmplz-subtitle{

	}

} // .cmplz-document

.cmplz-video {
	&.cmplz-hidden {
		visibility: hidden;
	}

	&.cmplz-iframe-styles {
	  background-color: transparent;
	}
} // .cmplz-video

.cmplz-blocked-content-container {
	background: var(--color-white);
	border: 0;
	border-radius: 3px;
	box-shadow: 0 0 1px 0 rgba(var(--color-black), 0.5), 0 1px 10px 0 rgba(var(--color-black), 0.15);
	display: flex;
	justify-content: center;
	align-items: center;
	background-repeat: no-repeat !important;
	background-size: cover !important;
	height: inherit;
	position: relative;

	iframe {
		max-height: 100%;
		border: 0 !important;
	}

	div {
		div {
			display: none;
		}
	}

	&.gmw-map-cover {
		max-height: 100%;
		position: absolute;
	}

	.cmplz-blocked-content-notice {
	  cursor: pointer;
	  position: absolute !important;
	  width: 50%;
	  top: 50%;
	  left: 50%;
	  transform: translate(-50%, -50%);
	  max-width: 200px;
	  font-size: 14px;
	  padding: 10px;
	  background-color: rgba(var(--color-black), 0.5);
	  /* Black background with opacity */
	  color: var(--color-white);
	  text-align: center;
	  /*make sure the container is on top of the iframe, but below our cookie notice */
	  z-index: 98;
	  /*We need some lineheight, for example Elementor will force a lineheight of 0*/
	  line-height: 23px;
	}
} // .cmplz-blocked-content-container

#cmplz-cookies-overview {
	// margin-bottom: $paragraph-margin-bottom;
  margin-bottom: 0;

	.cmplz-dropdown {

		+ .cmplz-dropdown {
			border-top: var(--default-border);
		}

		&[open] {
			.cmplz-service-header {
				background-color: var(--color-white);
				margin-bottom: 1rem;

				.marker {
					svg {
						transform: rotate(0);
					}
				}
			}
		}

		&:hover:not([open]) {
			background-color: var(--color-silver-light);
		}

		&.cmplz-dropdown-cookiepolicy {}

		> div {
			padding: spacing(s);

			> :last-child {
				margin-bottom: 0;
			}
		}

		.cmplz-title-wrap {
			> * {
				margin-top: 0;
			}
		}
	}

	.cmplz-service-header {
		display: flex;
		align-items: center;
		padding: spacing(s);
		flex-wrap: wrap;

		.marker {
			height: 32px;
			width: 32px;

			svg {
				height: 24px;
				width: 24px;
				transform: rotate(-90deg);
				transition: all .3s;
			}
		}

