			margin-left: auto;
			margin-bottom: 0;
			line-height: 1;
		}

		.cmplz-title-wrap{
			> * {
				font-size: fontsize-heading('s');
				flex-grow: 2;
				margin-bottom: 0;
				line-height: 1;
			}
		}
	}

	.cmplz-service-description {}

	.cmplz-sharing-data {}

	.cookies-per-purpose {

		&__row {
			display: flex;
			flex-wrap: wrap;

			.name-header,
			.retention-header,
			.function-header {
				border-top: var(--default-border);
				padding-top: spacing(s);
				width: 100%;

				h5 {
					margin-bottom: 0;
				}
			}

			.name,
			.retention,
			.function {
				width: 100%;
				margin-bottom: spacing(s);
			}

			@media only screen and (min-device-width: var(--breakpoint-m)) {
				.name-header,
				.retention-header,
				.function-header {
					width: percentage(33.33);
					order: 1;
				}

				.name,
				.retention,
				.function {
					width: percentage(33.33);
					order: 2;
				}
			}
		}

		.purpose {}
	}
}

