.services-list {
  @include media-breakpoint-down("sm") {
    margin: 30px 0 0 0;
  }

  margin: 60px 0 0 0;

  .service-content {
    @extend .question-answer;

    padding: 0;
  }

  #services-select {
    @include media-breakpoint-down("sm") {
      width: 100%;
      max-width: 100%;
    }

    margin: 0 0 30px 0;
    display: inline-block;
    width: auto;
    background: url("../images/icons/angle-down.svg") 90% center no-repeat $kuvittaja-yellow;
    border: none;
    border-radius: 0;
    -webkit-appearance: none;
    text-align: center;
    //font-family: $font-open-sans;
    font-family: $font-source-sans;
    font-weight: 600;
    height: 80px;
    padding-left: 40px;
  }

  .services-list-content {
    @include media-breakpoint-down("sm") {
      padding: 15px;
    }

    background-color: #faf7ca;
    background-size: cover;
    padding: 60px 30px;

    .read-more {
      @include media-breakpoint-down("sm") {
        margin-top: 25px;
        font-size: 15px;
      }

      @extend .btn;

      transition: box-shadow, 0.3s;
      margin-top: 20px;
      margin-bottom: 20px;
      background: #fff;
      color: #000;
      //font-family: $font-source-sans;
      font-family: $font-source-sans;
      font-size: 16px;
      font-weight: bold;
      text-transform: uppercase;
      padding: 15px 30px;
      border-radius: 30px;
      line-height: 1;
      text-decoration: none;

      &:hover {
        box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
      }

      &:focus {
        box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
      }
    }
  }

  .services-list-body {
    @include media-breakpoint-up("lg") {
      max-width: 65%;
    }
  }

  .services-list-body-text {
    @include media-breakpoint-down("sm") {
      padding: 20px;
    }

    background-color: $kuvittaja-pink-light;
    padding: 35px 40px;

    h2 {
      @include media-breakpoint-down("sm") {
        font-size: 22px;
        line-height: 32px;
        margin: 0 0 20px 0;
      }

      font-size: 28px;
      line-height: 36px;
      margin: 0 0 30px 0;
      font-weight: bold;
    }

    p,
    address,
    dl,
    ol,
    ul {
      font-size: 16px;
      //line-height: 20px;
      line-height: 1.25;
    }

    dl,
    ol,
    ul {
      line-height: 1.5;
    }

    .service-link {
      @include media-breakpoint-down("lg") {
        height: 180px;
        width: 180px;
      }

      @include media-breakpoint-down("md") {
        width: 180px;
        height: 180px;
      }

      @include media-breakpoint-down("sm") {
        flex: 0 0 150px;
        max-width: 150px;
        height: 150px;
        margin: 10px;
      }

      width: 220px;
      height: 220px;
      display: flex;
      align-items: center;
      background: #fff;
      border-radius: 50%;
      margin: 5px auto;

      a {
        display: block;
        padding: 0 15px;
        margin: 0 auto;
        text-align: center;
        color: $font-green;

        &::before {
          content: "";
          margin: 0;
        }
      }

      img {
        @include media-breakpoint-down("sm") {
          max-width: 90%;
        }

        max-width: 75%;
      }
    }

    .provider {
      text-align: center;
    }
  }
}



